import image from './images/1648807717562.jpg';



export default function About(params) {
    return(
        <div className="about" id="about">
            <div className="container py-5">
                <h1 className='head'>ABOUT HIMALAYAN CATS</h1>
                <div className="row mt-5">
                    <div className="col-md-6 mb-5 col-12">
                        <img className="d-block mx-auto col-md-10 col-12" src={image} alt=""/>
                    </div>
                    <div className="col-md-6 col-12">
                        <p className='text-left'>
                            HIMA started as one of Solana’s fastest growing memecoins and now we are growing into so much more. The goal of HIMA is to help build the Solana community through fun and enjoyment. We plan to create value for our community by creating NFTs with utility value, offering developer tools to the Solana ecosystem in the future, and creating a DAO around community.
                        </p>
                        <p className='text-left'>
                            Our first NFT collection, Himalayan Cat Crew is a collection of 555 cats looking for a cool home. Once you hold a Himalayan Cat Crew NFT, you will be given the benefits of the HimalayanCatDAO which include exclusive access to staking, monthly $HIMA lottery, fractionalized ownership of the NFTs in our vault, future airdrops and collections (Gen2 Cats and 3D Cats with metaverse integration), dividends from online merchandise sales (memecoinswag.com), and IRL or online events. In addition, holders will have access to our voting mechanism to decide the future of our DAO.
                        </p>
                    </div>
                </div>
            </div>
        </div>
    )
    
};
