import logo from './images/head-2.png'
import { Link } from 'react-router-dom'


export default function Navbar() {
    const styles={
        backgroundColor:'#141a28'
    }
    const textStyle={
        background:'linear-gradient(90deg, #e55d45 2.09%, #9c61a3 82.27%) !important',
        fontWeight:'700 !important'
    }
    function Toggler(){
        document.getElementById('target').classList.toggle('active-nav')
    }



    return(
        <nav className='sticky-top'>
        <nav className="navbar navbar-expand-md index sticky-top justify-content-between" style={styles}>
            <Link to='/'>
                <div className="navbar-brand d-flex align-items-center">
                    <img className="d-block" width="60" src={logo} alt="#"></img>
                    <h4 className="pl-2 m-0 textStyle d-none d-md-block">HIMALAYAN CATS</h4>
                </div>
            </Link>
            
            <a onClick={Toggler} className="navbar-toggler" id='toggler'>Menu</a>
            <div className="collapse navbar-collapse justify-content-center" >
            <div className='navbar-nav ml-auto justify-content-around'>
                
                <li className="nav-item textStyle"><a className="nav-link" href="#about">About US</a></li>
                <li className="nav-item textStyle"><a className="nav-link" href="#token">TOKENOMICS</a></li>
                {/* <li className="nav-item textStyle"><a className="nav-link" href="#">ROADMAP</a></li> */}
                <Link to='/construction'>
                <li className="nav-item textStyle"><a className="nav-link" href="#">NFT</a></li>
                </Link>
                
            </div>
            <div className='ml-auto'>
            <Link to='/constructionapp'>
            <a className='btn '>Launch App </a>
            </Link>
                
            </div>
            </div>
        </nav>
        <div onClick={Toggler} className="mobile-nav justify-content-center" id="target">
            <div className='navbar-nav justify-content-around'>
                <li className="nav-item textStyle"><a className="nav-link" href="#about">About US</a></li>
                <li className="nav-item textStyle"><a className="nav-link" href="#token">TOKENOMICS</a></li>
                {/* <li className="nav-item textStyle"><a className="nav-link" href="#">ROADMAP</a></li> */}
                <Link to='/constuction'></Link>
                <li className="nav-item textStyle"><a className="nav-link" href="#">NFT</a></li>
            </div>
            <div className=''>
            <Link to='/construction'></Link>
                <a className='btn '>Launch App </a>
            </div>
            </div>
        </nav>
    )
    
};
