import images from './images/1648659414490.png'
import Solana from './images/solana_color.png'


export default function Banner(){
    return (
    <div className="container">
        <div className="row align-items-center reverse heigth py-5">
            <div className="col-md-7 col-12 text-left">
                <h1 className="text-light text-left head">Solana's Biggest Cat Coin</h1>
                <p className="text-light text-left"> The coolest Cat coin in Solana Ecosystem, Join us and be a part of the coolest community.</p>
                <div className='row mx-auto'>
                    <a className="btn">LAUNCH APP </a>
                    <a className="btn ml-3">Whitepaper </a>
                </div>
                <div className='row mx-auto mt-4 align-items-center'>
                    <p className="m-0">Built on</p>
                    <img className="ml-1" src={Solana}/>
                </div>
            </div>
            <div className="col-md-5 mb-3 col-12"><img className="d-block col-12" src={images}/></div>
        </div>
    </div>)
    
}