import legend from './images/1648752025035.png'
import {Chart, Tooltip, Title, ArcElement } from 'chart.js'
import { Doughnut } from "react-chartjs-2";
Chart.register(Tooltip, Title, ArcElement);




export default function Tokenomics() {
    const styles={
        // backgroundImage:`url(${image}), linear-gradient(#141a28, #141a28)`,
        backgroundColor:'#141a28',
        backgroundSize:'100% 100%',
        backgroundRepeat:'no-repeat',
        position: 'relative'
    }
    const state = {
        labels: ['BURN', 'CIRCULATING SUPPLY', 'RELEASE', 'GRADUAL RELEASE',
                 'OTHERS', 'CORE TEAM'],
        datasets: [
          {
            label: 'Rainfall',
            backgroundColor: [
                'rgba(86, 233, 207, 1)',
                'rgba(198, 123, 243, 1)',
                'rgba(128, 81, 218, 1)',
                'rgba(218, 5, 171, 1)',
              'rgba(4, 172, 102, 1)',
              'rgba(70, 182, 140, 1)',
              
              
              
            ],
            // hoverBackgroundColor: [
            // '#501800',
            // '#4B5000',
            // '#175000',
            // '#003350'
            // ],
            data: [60, 17.5, 2.5, 5, 10, 5]
          }
        ]
      }
      return(
        <div style={styles} id='token'>
            <div id='dist' className='container mt-5 py-5 ' > 
           <h1 className='text-center mb-5 wow fadeInUp text-light head'>TOKENOMICS</h1>
          <div className='row align-items-center'>
          <div className='col-md-5 col-8 mx-auto wow fadeInUp'>
              <Doughnut
            data={state}
            options={{
              
              htmlLegend: {
                // ID of the container to put the legend in
                containerID: 'legend-container',
              },
              legend:{
                display:false,
              },
              defaults:{
                legend:false
              }
            }}
          />
        </div>
        <div className='wow col-md-5 col-12 mt-5 fadeInUp'>
          <img className='d-block col-12' src={legend}/>
        </div>
          </div>
        </div>
        </div>
        
      )
};
