import image from './images/construction.png';
export default function Construction(params) {
    return(
        <div className="container align-items-center py-5" style={{minHeight:"80vh"}}>
            <img src={image} alt="construction" className="d-block mx-auto col-md-4 col-10" />
            <h1>Site Under is Construction</h1>
        </div>
    )
};
